/*Plugin CSS*/
.str_wrap {
	overflow:hidden;
	width:100%;
	font-size:12px;
	line-height:16px;
	position:relative;
	-moz-user-select: none;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;  
	background:#f6f6f6;
	white-space:nowrap;
}
.str_wrap.str_active {
	background:#f1f1f1;
}
.str_move { 
	white-space:nowrap;
	position:absolute; 
	top:0; 
	left:0;
	cursor:move;
}
.str_move_clone {
	display:inline-block;
	vertical-align:top;
	position:absolute; 
	left:100%;
	top:0;
}
.str_vertical .str_move_clone {
	left:0;
	top:100%;
}
.str_down .str_move_clone {
	left:0;
	bottom:100%;
}
.str_vertical .str_move,
.str_down .str_move {
	white-space:normal;
	width:100%;
}
.str_static .str_move,
.no_drag .str_move,
.noStop .str_move{
	cursor:inherit;
}
.str_wrap img {
	max-width:none !important;
}
* {
  margin: 0;
  padding: 0;
}
.wrap {
  width: 90%;
  margin: 10px auto;
  padding: 0;
  font-size: 0 !important;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  border: 6px solid #000;
}
.wrap img {
  width: 200px;
  height: 180px;
  vertical-align: bottom;
  border: 2px solid #000;
}
